  font-family: Arial, sansâ€‘serif;
  color: #333;
}

/* Hero */
.aboutus-hero {
  background-image: url("https://i.postimg.cc/SsdRrXJJ/i-Stock-824369786-1.webp");
  background-size: cover;
  background-position: center;
  position: relative;
  min-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 769px) {
  .aboutus-hero {
    background-image: url("https://i.postimg.cc/CMDQ2vFs/i-Stock-824369786-2-2.webp");
  }
}
.aboutus-hero-overlay {
  background-color: rgba(0, 0, 0, .4);
  padding: 2rem;
  text-align: center;
  color: #fff;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  border-radius: 8px;
}
.aboutus-hero-title {
  font-size: 2rem;
  margin: 0 0 .5rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.aboutus-hero-subtitle {
  font-size: 1.1rem;
  margin: 0;
  font-weight: 300;
  opacity: .95;
}

/* About Us Cards */
.aboutus-section {
  padding: 2rem 1rem;
  background-color: #f9f9f9;
}
.aboutus-heading {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #354d4b;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.aboutus-content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.aboutus-card {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .06);
  transition: transform .3s ease;
}
.aboutus-card:hover {
  transform: translateY(-3px);
}
.aboutus-card h3 {
  margin-top: 0;
  margin-bottom: .5rem;
  font-size: 1.1rem;
  color: #333;
  text-transform: uppercase;
  letter-spacing: .5px;
}
.aboutus-card p {
  margin: 0;
  font-size: .95rem;
  line-height: 1.5;
  color: #555;
}

/* Providers Section */
.aboutus-provider {
  padding: 2rem 1rem;
}
.aboutus-provider-title {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #354d4b;
  letter-spacing: 1px;
  text-transform: uppercase;
}

/* Use grid for provider cards */
.aboutus-provider-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

/* Individual provider card flexes column on mobile */
.aboutus-provider-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

/* Photo */
.aboutus-provider-photo {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .06);
}

/* Info panel */
.aboutus-provider-info {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .06);
  transition: transform .3s ease;
}
.aboutus-provider-info:hover {
  transform: translateY(-3px);
}
.aboutus-provider-info h3 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  text-align: center;
  color: #333;
  text-transform: uppercase;
  letter-spacing: .5px;
}
.provider-cert {
  font-style: italic;
  color: var(--brand-secondary);
  margin-bottom: .5rem;
}
.aboutus-provider-info p {
  font-size: .95rem;
  color: #555;
  margin-bottom: 1rem;
  line-height: 1.5;
}

/* Services Grid */
.aboutus-services {
  padding: 2rem 1rem;
  background-color: #f0f0f0;
}
.aboutus-services-title {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 2rem;
  color: #354d4b;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.aboutus-services-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
.aboutus-service-card {
  background-color: #fff;
  text-decoration: none;
  color: #333;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .06);
  display: flex;
  flex-direction: column;
  transition: transform .3s ease;
}
.aboutus-service-card:hover {
  transform: translateY(-3px);
}
.aboutus-service-image {
  width: 100%;
  height: 150px;
  background-size: cover;
  background-position: center;
}
.aboutus-service-title {
  font-size: 1rem;
  margin: .75rem;
  text-transform: uppercase;
  letter-spacing: .5px;
}
.aboutus-service-desc {
  font-size: .85rem;
  color: #666;
  margin: 0 .75rem .75rem;
  line-height: 1.4;
}

/* ===== Larger screens ===== */
@media (min-width: 768px) {
  /* About Us cards sideâ€‘byâ€‘side */
  .aboutus-content {
    flex-direction: row;
  }
  .aboutus-card {
    flex: 1;
  }

  /* Providers: twoâ€‘column grid, card flex to row */
  .aboutus-provider-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  .aboutus-provider-card {
    flex-direction: row;
    align-items: flex-start;
  }
  .aboutus-provider-photo {
    flex-shrink: 0;
  }
  .aboutus-provider-info {
    margin-left: 1rem;
  }

  /* Services grid becomes multiâ€‘col */
  .aboutus-services-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}
